.media-header {
  margin-top: 48px;
  width: 100vw;
  left: 0;
  position: relative;
  overflow: hidden;
  padding: 80px 0 20px;
}

.media-header__wrap>* {
  margin: 0;
  /* font-weight: 600; */
  line-height: 1.3;
}
.media-header__wrap {
  margin-left: 15px;
  line-height: 1.3;
}
.media-header__background {
  position: absolute;
  top: -3px;
  left: -3px;
  bottom: -3px;
  right: -3px;
  background-size: cover;
  background-position: 50%;
  -webkit-filter: blur(1px);
  filter: blur(1px);
  background-image: url(/uploads/cover/buccafe/cover/cover_250x350.jpg?1592549516);
}
.media-header__background:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
  background: rgba(0,0,0,.65);
}
.media-header__body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: #fff;
  position: relative;
  z-index: 4;
  padding: 0 10px;
}
.media-header__cover {
  width: 80px;
  display: block;
  border-radius: 6px;
}
.media-header__name {
  font-size: 18px;
}
.media-header__alt-name {
  font-size: 14px;
  font-weight: 400;
}
.media-rating {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: baseline;
  -ms-flex-align: baseline;
  align-items: baseline;
  margin-top: 15px;
}
.media-rating__value {
  font-size: 16px;
  font-weight: 700;
  margin: 0 6px;
}
.media-rating__votes {
  opacity: .8;
  font-size: 12px;
}

.media-header__background_blurred {
  top: -14px;
  left: -14px;
  right: -14px;
  bottom: -14px;
  -webkit-filter: blur(8px);
  filter: blur(8px);
}

.tabs__wrapper {
  -webkit-box-shadow: inset 0 -1px 0 #e5e5e5;
  box-shadow: inset 0 -1px 0 #e5e5e5;
}